html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

@font-face { font-family: "OpenSans"; font-style: normal; font-weight: 200; src: local("OpenSans-Light"), local("OpenSans-Light"), url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff2") format("woff2"); }

@font-face { font-family: "OpenSans"; font-style: normal; font-weight: 400; src: local("OpenSans-Regular"), local("OpenSans-Regular"), url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.woff2") format("woff2"); }

@font-face { font-family: "OpenSans"; font-style: normal; font-weight: 600; src: local("OpenSans-SemiBold"), local("OpenSans-SemiBold"), url("../fonts/OpenSans-SemiBold.woff") format("woff"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2"); }

@font-face { font-family: "OpenSans"; font-style: normal; font-weight: 700; src: local("OpenSans-Bold"), local("OpenSans-Bold"), url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.woff2") format("woff2"); }

@font-face { font-family: "OpenSans"; font-style: normal; font-weight: 800; src: local("OpenSans-ExtraBold"), local("OpenSans-ExtraBold"), url("../fonts/OpenSans-ExtraBold.woff") format("woff"), url("../fonts/OpenSans-ExtraBold.woff2") format("woff2"); }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.icon-checked { width: 1em; height: 1em; fill: initial; }

.icon-close { width: 1em; height: 1em; fill: initial; }

.icon-facebook { width: 1em; height: 1em; fill: initial; }

.icon-instagram { width: 1em; height: 1em; fill: initial; }

.icon-logo { width: 5.66em; height: 1em; fill: initial; }

.icon-twitter { width: 1em; height: 1em; fill: initial; }

.icon-vkontakte { width: 1em; height: 1em; fill: #030104; }

body { background: #ffffff; min-width: 320px; line-height: 1.4; font-family: "OpenSans"; }

.container { position: relative; margin: 0 auto; width: 100%; height: 100vh; color: #000000; background-position: 50% 50%; background-repeat: no-repeat; background-size: cover; }

/*Start header*/
.header { display: flex; align-items: center; justify-content: space-between; position: absolute; top: 0; left: 0; right: 0; padding: 30px; background-color: rgba(255, 255, 255, 0.65); z-index: 8; }

.header.bg { background-color: #eee; box-shadow: 0 0 20px rgba(0, 0, 0, 0.4); }

@media only screen and (max-width: 1023px) { .header { padding: 20px 30px 20px; } }

.header__logo-link { position: relative; display: block; max-width: 250px; font-size: 0; z-index: 10; }

.header__logo-link .icon { width: 100%; height: 50px; fill: #000000; }

@media only screen and (max-width: 1023px) { .header__logo-link { max-width: 200px; }
  .header__logo-link .icon { height: 40px; } }

@media only screen and (max-width: 767px) { .header__logo-link { max-width: 150px; }
  .header__logo-link .icon { height: 30px; } }

.header__mob-menu-toggle { display: none; position: absolute; top: 30px; right: 30px; width: 30px; height: 30px; background-color: rgba(0, 0, 0, 0); cursor: pointer; z-index: 9; }

@media only screen and (max-width: 1023px) { .header__mob-menu-toggle { top: 25px; display: block; } }

.header__mob-menu-toggle span { position: absolute; display: block; width: 30px; height: 4px; background-color: #000000; top: 50%; left: 50%; transform: translate(-50%, -50%); box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2); border-radius: 2px; transition: .3s; transition-delay: .3s; outline: none; }

.header__mob-menu-toggle span::before { position: absolute; display: block; content: ''; width: 100%; height: 100%; background-color: #000000; top: -8px; left: 0; box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2); border-radius: 2px; transition-property: top , -webkit-transform; transition-property: top , transform; transition-property: top , transform , -webkit-transform; transition-delay: 0.3s, 0s; transition-duration: 0.3s, 0.3s; }

.header__mob-menu-toggle span::after { position: absolute; display: block; content: ''; width: 100%; height: 100%; background-color: #000000; top: 8px; left: 0; box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2); border-radius: 2px; transition-property: top , -webkit-transform; transition-property: top , transform; transition-property: top , transform , -webkit-transform; transition-delay: 0.3s, 0s; transition-duration: 0.3s, 0.3s; }

.header__mob-menu-toggle.active span::before { top: 0; -webkit-transform: rotate(45deg); -webkit-transform-transform: rotate(45deg); transition-delay: 0s, 0.3s; }

.header__mob-menu-toggle.active span::after { top: 0; -webkit-transform: rotate(-45deg); -webkit-transform-transform: rotate(-45deg); transition-delay: 0s, 0.3s; }

.header__mob-menu-toggle.active span { background: transparent; box-shadow: 0 2px 2px rgba(0, 0, 0, 0); }

@media only screen and (max-width: 1023px) { .header__social { display: none; } }

/*End mobile menu*/
@media only screen and (max-width: 1023px) { .navigation { display: none; position: fixed; top: 0; right: 0; left: 0; width: 100%; background-color: #ffffff; overflow: auto; } }

.navigation__item-mobile { display: none; }

@media only screen and (max-width: 1023px) { .navigation__item-mobile { display: block; } }

.navigation__menu { display: flex; align-items: center; }

@media only screen and (max-width: 1023px) { .navigation__menu { flex-direction: column; padding-top: 70px; padding-bottom: 20px; } }

.navigation__item { margin-right: 25px; }

@media only screen and (max-width: 1023px) { .navigation__item { margin-right: 0; text-align: center; padding: 20px 0; } }

.navigation__link { position: relative; color: rgba(0, 0, 0, 0.8); transition: color .3s; text-decoration: none; font-family: "OpenSans"; font-weight: 600; text-transform: uppercase; font-size: 16px; }

@media only screen and (max-width: 767px) { .navigation__link { font-size: 12px; } }

.navigation__link:before { position: absolute; content: ''; width: 100%; height: 2px; background-color: black; bottom: -3px; left: 0; transform: scaleX(0); transition: transform .3s; }

.navigation__link:hover:before { transform: scaleX(1); }

.navigation__link.active:before { transform: scaleX(1); }

.social__block { position: relative; z-index: 10; }

.social__link { display: inline-block; text-decoration: none; font-size: 0; }

.social__link:not(:last-child) { margin-right: 10px; }

@media only screen and (max-width: 1023px) { .social__link:not(:last-child) { margin-right: 15px; } }

.social__link .icon { width: 25px; height: 25px; fill: #000000; transition: fill .3s, background-color .3s; border-radius: 5px; }

.social__link:hover.social__link--facebook .icon { background-color: #ffffff; fill: #0753f7; }

.social__link:hover.social__link--instagram .icon { background-color: #ffffff; fill: #ee2a7b; }

.social__link:hover.social__link--twitter .icon { background-color: #ffffff; fill: #07b7f7; }

.social__link:hover.social__link--vkontakte .icon { background-color: #ffffff; fill: #5181e8; }

.slider { overflow: hidden; }

.slider__wrap { position: relative; height: 100vh; width: 100%; white-space: nowrap; opacity: 0; visibility: hidden; transition: opacity .5s, visibility .5s; font-size: 0; }

.slider__wrap.slick-initialized { overflow: visible; opacity: 1; visibility: visible; }

.slider__prev-button { position: absolute; background: none; border: none; top: 0; bottom: 0; left: 0; width: 50%; z-index: 2; color: rgba(255, 255, 255, 0.1); font-size: 80px; background-color: rgba(255, 255, 255, 0); outline: none; opacity: 0; text-align: left; transition: opacity .3s; }

.slider__prev-button:hover { opacity: 1; cursor: pointer; }

.slider__next-button { position: absolute; background: none; border: none; top: 0; bottom: 0; right: 0; width: 50%; z-index: 2; color: rgba(255, 255, 255, 0.1); font-size: 80px; background-color: rgba(255, 255, 255, 0); outline: none; opacity: 0; text-align: right; transition: opacity .3s; }

.slider__next-button i { z-index: 3; }

.slider__next-button:hover { opacity: 1; cursor: pointer; }

.slide { height: 100vh; width: 100vw; background-repeat: no-repeat; background-size: cover; background-position: 50% 20%; }

/*Contact page*/
.wrapper.image-bg { width: 100%; height: 100vh; background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; }

@media only screen and (max-width: 767px) { .wrapper.image-bg { height: auto; padding-bottom: 40px; } }

.right-line-title, .left-line-title { display: block; width: 20%; height: 1px; background-color: #000000; }

.right-line-title { margin-left: 15px; }

.left-line-title { margin-right: 15px; }

.contact { margin: 0 auto; max-width: 550px; padding: 125px 0 0; overflow: auto; font-family: "OpenSans"; }

@media only screen and (max-width: 767px) { .contact { padding: 120px 0 0; } }

.contact__bg { padding: 25px 20px 30px; background-color: rgba(255, 255, 255, 0.75); box-shadow: 0 0 15px rgba(0, 0, 0, 0.3); border-radius: 4px; z-index: -2; }

.contact__wrap { width: 100%; }

.contact__form { position: relative; font-family: "OpenSans"; }

.contact__title-block { display: flex; align-items: center; justify-content: center; margin-bottom: 20px; }

.contact__form-title { position: relative; font-size: 25px; font-weight: 700; white-space: nowrap; }

.contact__castom-font { font-size: 16px; margin-bottom: 15px; text-align: center; }

.contact__text { display: flex; flex-direction: column; justify-content: center; align-items: center; margin-bottom: 15px; }

.contact__text .social { margin-bottom: 15px; }

.contact__text .social__block { z-index: 5; }

.contact__phone, .contact__email { font-size: 16px; font-weight: 700; margin-bottom: 5px; }

.contact__phone-number { font-size: 16px; font-weight: 400; }

.contact__email { margin-top: 10px; }

.contact__form-row { position: relative; width: 80%; margin: 0 auto 15px; transition: .3s; margin-bottom: 25px; }

.contact__form-row--mod { margin-bottom: 0; }

.contact__input { z-index: 1; width: 100%; padding: 5px 10px 5px; background-color: inherit; border: none; border-bottom: 1px solid rgba(0, 0, 0, 0.5); outline: none; transition: .3s; font-size: 16px; }

.contact__input::placeholder { font-size: 15px; font-weight: 400; color: #000000; }

.contact__input:hover { border-bottom: 1px solid black; }

.contact__input:focus { border-bottom: 1px solid black; }

.contact__textarea.error, .contact__input.error { border-bottom-color: red; }

.contact__textarea.valid, .contact__input.valid { border-bottom-color: #57c147; }

.contact__textarea { width: 100%; height: 60px; border: none; outline: none; background-color: inherit; border-bottom: 1px solid black; resize: none; font-size: 16px; font-family: sans-serif; padding: 5px 10px 5px; }

.contact__textarea::placeholder { font-size: 15px; font-weight: 400; color: #000000; }

.contact__button { position: relative; border: none; background: none; color: #000000; font-size: 16px; border: 1px solid #000000; border-radius: 4px; padding: 15px; width: 100%; cursor: pointer; transition: color .3s; outline: none; z-index: 0; }

.contact__button:before { position: absolute; content: ''; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.9); transform: scaleX(0); transition: transform .3s; z-index: -1; }

.contact__button:hover { color: #fff; }

.contact__button:hover:before { transform: scaleX(1); }

.contact__button:hover:disabled { color: #000000; cursor: default; }

.contact__button:hover:disabled:before { transform: scaleX(0); }

#name-error, #email-error, #phone-error { position: absolute; left: 15px; bottom: -20px; font-size: 12px; color: red; transition: color .3s; }

#message-error { position: absolute; left: 15px; bottom: -12px; font-size: 12px; color: red; transition: color .3s; }

.success, .success-error { position: absolute; display: none; bottom: -18px; left: 0; width: 100%; text-align: center; font-size: 12px; font-family: sans-serif; }

.success { color: #000000; }

.success-error { color: red; }

.team { padding: 160px  0 0; color: #000; }

.team__wrap { max-width: 800px; padding: 15px; margin: 0 auto; }

.team__wrap h2 { position: relative; font-size: 20px; font-weight: 700; white-space: nowrap; text-align: center; margin-bottom: 25px; }

.team__row { display: flex; align-items: center; justify-content: space-between; margin-bottom: 35px; }

.team__row-img { flex: 1 1 40%; height: 0; padding-bottom: 30%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; margin: 20px; box-shadow: 0 0 20px rgba(0, 0, 0, 0.5); }

.team__row-text { flex: 1 1 50%; padding: 20px; }

.team__row-text p { font-size: 16px; line-height: 22px; max-width: 800px; }

.team__row-text p span { font-weight: 600; font-size: 18px; }

.team__row-title { position: relative; font-size: 18px; font-weight: 700; margin-bottom: 25px; }

.team__row-title.left { text-align: left; }

.team__row-title.right { text-align: right; }

.team__row-title.center { text-align: center; }

.team__footer { text-align: center; }

.price { width: 100%; }

.price { padding: 150px 0 0; font-family: "OpenSans"; }

@media only screen and (max-width: 1023px) { .price { padding: 120px 0 0; } }

.price__wrap { max-width: 800px; margin: 0 auto; padding: 25px 20px 30px; background-color: rgba(255, 255, 255, 0.75); box-shadow: 0 0 15px rgba(0, 0, 0, 0.3); border-radius: 4px; }

.price__title { text-align: center; margin-bottom: 20px; }

@media only screen and (max-width: 1023px) { .price__title { margin-bottom: 10px; } }

.price__text { text-align: center; margin-bottom: 20px; }

@media only screen and (max-width: 1023px) { .price__text { margin-bottom: 10px; } }

.price__header { display: flex; -ms-align-items: center; align-items: center; justify-content: space-between; max-width: 100%; margin: 0 auto 15px; }

.price__header-circle { position: relative; display: block; margin: 0 20px; border: 2px solid gold; border-radius: 50%; background-color: inherit; font-size: 0; margin-top: 2px; min-width: 16px; height: 16px; flex: none; }

.price__header-circle .icon { position: absolute; width: 10px; height: 10px; top: 50%; left: 50%; transform: translate(-50%, -50%); opacity: .8; fill: #000000; }

.price__row { display: flex; justify-content: center; margin-bottom: 20px; }

@media only screen and (max-width: 767px) { .price__row { flex-direction: column; } }

.price__col { margin: 20px; }

@media only screen and (max-width: 1023px) { .price__col { margin: 10px; } }

.price__col:first-child { margin-right: 20px; }

@media only screen and (max-width: 1023px) { .price__col:first-child { margin-right: 10px; } }

.price__col:last-child { margin-left: 20px; }

@media only screen and (max-width: 1023px) { .price__col:last-child { margin-left: 10px; } }

.price__col.price__item { line-height: 1.6; }

.price__item { display: flex; align-items: flex-start; justify-content: flex-start; margin-bottom: 5px; }

.price__item .strong { display: inline-block; margin-left: 3px; font-weight: 700; }

.price__button { text-align: center; }

.price__btn { position: relative; border: none; background: none; color: #000000; font-size: 16px; border: 1px solid #000000; border-radius: 4px; padding: 15px; cursor: pointer; transition: color .3s; outline: none; z-index: 0; }

.price__btn:before { position: absolute; content: ''; top: 0; left: 0; width: 100%; height: 100%; background-color: rgba(0, 0, 0, 0.9); transform: scaleX(0); transition: transform .3s; z-index: -1; }

.price__btn:hover { color: #fff; }

.price__btn:hover:before { transform: scaleX(1); }

.price__btn:hover:disabled { color: #000000; cursor: default; }

.price__btn:hover:disabled:before { transform: scaleX(0); }

.contact.hide { position: fixed; min-width: 280px; min-height: 0px; max-width: 600px; width: 100%; border-radius: 5px; background: #ffffff; top: 50%; left: 50%; display: none; opacity: 1; z-index: 100; transform: translate(-50%, -50%); padding: 0; }

.contact__bg { padding: 50px 30px; }

.contact__form { position: relative; }

.contact__close { background: none; position: absolute; width: 20px; height: 20px; top: -30px; right: -10px; text-align: right; }

.contact__close .icon { transition: fill .3s; width: 20px; height: 20px; fill: gray; }

.contact__close:hover .icon { fill: #000000; }

.contact__select { width: 100%; border: none; border-bottom: 1px solid rgba(0, 0, 0, 0.5); padding: 5px 8px; outline: none; font-size: 15px; font-weight: 400; color: #000000; }

.overlay { z-index: 90; position: fixed; background-color: #000000; opacity: 0.8; filter: alpha(opacity=80); top: 0; right: 0; bottom: 0; left: 0; cursor: pointer; display: none; }
